.row {
  display: flex;
  /* position: absolute; */
  top: 25%;
  width: 100%;
  /* align-items: center; */
  justify-content: center;
  /* flex-direction: column; */
  /* margin-right: -15px;
  margin-bottom: -15px;
  margin-left: -15px;
  flex-wrap: wrap; */
  /* align-content: stretch; */
  align-items: center;
}

.col {
  /* justify-content: ; */
  margin-bottom: 15px;
  padding-right: 15px;
  padding-left: 15px;
  /* flex: 1; */
  /* max-width: 50%; */
  flex-basis: 50%;
}

.login__header {
  color: #2cbed8;
  /* text-transform: uppercase; */
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  margin-bottom: 10px;
}

.login__description {
  color: #000;
}

.login__div-input {
  position: relative;
  padding-top: 20px;
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;
}

.form-username {
  font-size: 16px;
  position: relative;
  height: 50px;
  overflow: hidden;
  /* background: blue; */
}

.form-username input {
  width: 100%;
  height: 100%;
  color: #595f6e;
  padding-top: 20px;
  border: none;
  outline: none;
  /* height: 40px; */
}

.form-username label {
  position: absolute;
  bottom: 0px;
  left: 0%;
  width: 100%;
  height: 100%;
  /* background: blue; */
  pointer-events: none;
  border-bottom: 1px solid black;
}

.form-username label::after {
  content: "";
  position: absolute;
  left: 0px;
  bottom: -1px;
  height: 100%;
  width: 100%;
  border-bottom: 2px solid #5fa8d3;

  transform: translateX(-100%);
}

.content-name {
  position: absolute;
  bottom: 5px;
  left: 0px;
  transition: all 0.1s ease;
}

.form-username input:focus + .label-name .content-name,
.form-username input:valid + .label-name .content-name {
  transform: translateY(-150%);
  font-size: 14px;
  color: #5fa8d3;
}

.form-username input:focus + .label-name::after,
.form-username input:valid + .label-name::after {
  transform: translateX(0%);
  transition: transform 0.3s ease;
}

